.service-container {
  margin: 3rem;
  opacity: 0;
}

.service-container.reveal {
  opacity: 1;
  transform: translateY(0);
}

.service-cards-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 320px);
  gap: 5rem;
  justify-content: center;
  align-items: center;
}

.service-id {
  color: var(--color-3);
  font-size: 15px;
  padding: 2rem 0 0 0;
}

.service-card {
  background-color: var(--glass-bg);
  backdrop-filter: blur(10px);
  padding: 20px;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 2rem;
  transition: transform 0.5s;
  height: 100%;
}

.service-card:hover {
  transform: scale(1.05);
  background-color: rgba(255, 131, 197, 0.103);
  border: solid 0.01px var(--stroke-color);
}

.service-title {
  color: var(--color-3);
  font-size: 18px;
  margin: 10px 0;
  text-align: left;
  font-weight: bold;
}

.service-description {
  color: var(--color-2);
  font-size: 14px;
  text-align: left;
  line-height: 1.5;
}
@media screen and (max-width: 1100px) {
  .service-container {
    margin: 10rem 3rem 3rem 3rem;
    align-items: center;
  }

  .service-cards-container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(5, auto); /* Set height to auto */
    gap: 5rem;
    justify-content: center;
    align-items: center;
    margin: 2rem;
  }
  .service-card {
    backdrop-filter: blur(10px);
    padding: 20px;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 0;
    transition: transform 0.5s;
    height: 100%;
    align-self: center;
  }
}
@media screen and (max-width: 480px) {
  .service-container {
    margin: 10rem 1rem 1rem 1rem;
    align-items: center;
  }

  .service-cards-container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(5, auto); /* Set height to auto */
    gap: 5rem;
    justify-content: center;
    align-items: center;
    margin: 2rem;
  }
  .service-card {
    backdrop-filter: blur(10px);
    padding: 20px;
    justify-content: center;
    align-items: center;
    border-radius: 10px;

    margin-bottom: 0;
    transition: transform 0.5s;
    height: 100%;
    align-self: center;
  }
}
